body {
	margin: 0;
	font-family: 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	background-image: linear-gradient(
		89deg,
		rgba(9, 9, 121, 1) 11.2%,
		rgba(144, 6, 161, 1) 53.7%,
		rgba(0, 212, 255, 1) 100.2%
	);
}

button {
	cursor: pointer;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

.bounding-box {
	position: absolute;
	box-shadow: 0 0 0 3px #149df2 inset;
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	cursor: pointer;
}

.form {
	width: 700px;
	background: repeating-radial-gradient(
			circle at bottom left,
			#fedc00 0,
			#fedc00 5.5555555556%,
			#fcb712 0,
			#fcb712 11.1111111111%,
			#f7921e 0,
			#f7921e 16.6666666667%,
			#e87f24 0,
			#e87f24 22.2222222222%,
			#dd6227 0,
			#dd6227 27.7777777778%,
			#dc4c27 0,
			#dc4c27 33.3333333333%,
			#ca3435 0,
			#ca3435 38.8888888889%,
			#b82841 0,
			#b82841 44.4444444444%,
			#953751 0,
			#953751 50%,
			#364c88 0,
			#364c88 55.5555555556%,
			#16599d 0,
			#16599d 61.1111111111%,
			#02609e 0,
			#02609e 66.6666666667%,
			#0073a9 0,
			#0073a9 72.2222222222%,
			#008aa4 0,
			#008aa4 77.7777777778%,
			#239a87 0,
			#239a87 83.3333333333%,
			#7cba6d 0,
			#7cba6d 88.8888888889%,
			#becc2f 0,
			#becc2f 94.4444444444%,
			#e0d81d 0,
			#e0d81d 100%
		),
		repeating-radial-gradient(
			circle at bottom right,
			#fedc00 0,
			#fedc00 5.5555555556%,
			#fcb712 0,
			#fcb712 11.1111111111%,
			#f7921e 0,
