			#239a87 0,
			#239a87 83.3333333333%,
			#7cba6d 0,
			#7cba6d 88.8888888889%,
			#becc2f 0,
			#becc2f 94.4444444444%,
			#e0d81d 0,
			#e0d81d 100%
		);
}

.Tilt {
	background-image: linear-gradient(
		150deg,
		rgba(9, 9, 121, 1) 11.2%,
		rgba(144, 6, 161, 1) 53.7%,
		rgba(0, 212, 255, 1) 100.2%
	);
}

.App {
	text-align: center;
}

.center {
	display: flex;
	justify-content: center;
}

.particles {
	position: fixed;
	top: 0;
	bottom: 0;
	right: 0;
	left: 0;
	z-index: -1;
}

